body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.calendar {
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: flex;
  align-items: center;
  padding: 10px;
  gap: 1px;
  flex-wrap: wrap;
  width: 706px;
  margin: 0 auto;
}

.calendar__day {
  position: relative;
  height: 100px;
  width: 100px;
  background-color: #eee;
  border: 1px solid black;
  box-sizing: border-box;
  transition: 500ms;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

.calendar--start-day-tue .calendar__day:nth-child(1) {
  margin-left: 101px;
}

.calendar--start-day-wed .calendar__day:nth-child(1) {
